.brands {
	background-color: #fff;
	&__in {
		padding: 15px;
		overflow: hidden; } }
.brands-list {
	font-size: 0;
	@include rmin(1700) {
		@include justify; }
	@include r(1700) {
		text-align: left; }
	@include lg {
		text-align: center; }
	@include md {
		margin-top: -30px; }
	&__item {
		display: inline-block;
		vertical-align: middle;
		@include md {
			padding: 30px 30px 0 0; }
		&:not(:last-child) {
			@include md-min {
				padding-right: 30px; } } }
	img {
		display: block;
		max-width: 100%; } }
