.tabs {
	text-align: left;
	@include clr;
	&__control {
		@include sm-min {
			float: left;
			width: 142px;
			min-height: 1px; }
		@include sm {
			margin-left: -30px; } }
	&__control-item {
		&:not(:first-of-type) {
			@include sm-min {
				margin-top: 20px; } }
		@include sm {
			width: 33.3333%;
			display: inline-block;
			vertical-align: top;
			padding-left: 30px; } }
	&__content {
		position: relative;
		@include sm-min {
			float: right;
			width: 592px; } }
	&__link {
		font-size: 24px;
		line-height: 1.2;
		color: #fff;
		display: block;
		padding-bottom: 36px;
		color: rgba(#fff, 0.4);
		transition: all 0.3s;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 10px;
			transform: scaleY(0.5);
			transition: all 0.3s;
			background-color: rgba(#fff, 0.4); }
		&:hover,
		&:focus,
		&.is-active {
			color: #fff;
			&:after {
				transform: scaleY(1);
				background-color: #fff; } } } }
.info {
	@include roboto-bold;
	font-size: 24px;
	max-width: 380px;
	text-align: right;
	display: inline-block;
	position: absolute;
	right: 0;
	top: -210px;
	@include sm {
		top: -263px; } }
