  font-size: 14px;
}

.btn:hover, .btn:focus {
  background-color: #33bbff;
}

.btn:active {
  background-color: #0099e6;
}

.btn-primary {
  background-color: #ff1799;
  padding: 27px 60px;
  color: #fafafc;
  font-size: 20px;
  font-family: "Robotomono Bold", Arial, sans-serif;
  font-weight: normal;
}

@media only screen and (max-width: 1200px) {
  .btn-primary {
    font-size: 16px;
    padding: 24px 34px;
  }
}

.btn-primary:hover, .btn-primary:focus {
  background-color: #ff31a4;
}

.btn-primary:active {
  background-color: #fd008d;
}

.btn-primary_blue {
  background-color: #00aaff;
}

.btn-primary_blue:hover, .btn-primary_blue:focus {
  background-color: #33bbff;
}

.btn-primary_blue:active {
  background-color: #0099e6;
}

.btn-secondary {
  line-height: 1;
  color: #0ca8fd;
  font-size: 20px;
  font-size: calc(16px + (20 - 16) * ((100vw - 900px) / (1920 - 900)));
  font-family: "Robotomono Bold", Arial, sans-serif;
  font-weight: normal;
  border: 2px solid #0ca8fd;
  border-radius: 37px;
  padding: 25px 70px;
  overflow: hidden;
}

@media (max-width: 900px) {
  .btn-secondary {
    font-size: 16px;
  }
}

@media (min-width: 1920px) {
  .btn-secondary {
    font-size: 20px;
  }
}

.btn-secondary:hover, .btn-secondary:focus {
  border-color: #fb1d95;
}

.btn-secondary:hover .btn-secondary__in, .btn-secondary:focus .btn-secondary__in {
  right: 0;
}

.btn-secondary__in {
  position: absolute;
  top: -2px;
  bottom: -2px;
  width: 100%;
  top: 0;
  right: 120%;
  background-color: #fb1d95;
  display: block;
  transition: right 0.3s;
}

.btn-secondary__text {
  position: relative;
  z-index: 2;
}

.btn-menu {
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  width: 28px;
  height: 20px;
  position: relative;
  overflow: hidden;
  top: 1px;
  background-color: transparent;
  display: inline-block;
  vertical-align: middle;
  margin-left: 14px;
}

.btn-menu span {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  margin-top: -1px;
  height: 3px;
  background-color: #fff;
  display: block;
  transition: transform 0.2s;
}

.btn-menu span::after, .btn-menu span::before {
  content: '';
  display: block;
  position: absolute;
  height: 3px;
  background-color: #fff;
  left: 0;
  right: 0;
}

.btn-menu span::before {
  top: -8px;
  transform-origin: top right;
  transition: transform 0.2s, width 0.2s, top 0.2s;
}

.btn-menu span::after {
  top: 8px;
  transform-origin: bottom right;
  transition: transform 0.2s, width 0.2s, bottom 0.2s;
}

.btn-menu.is-active span {
  transform: rotate(180deg);
}

.btn-menu.is-active span::before, .btn-menu.is-active span::after {
  width: 40%;
}

.btn-menu.is-active span::before {
  transform: translateX(15px) translateY(9px) rotate(-45deg);
}

.btn-menu.is-active span::after {
  transform: translateX(14px) translateY(-9px) rotate(45deg);
}

.btn-close {
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  right: 5px;
  background: transparent;
  font-family: "Roboto Medium", Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  color: #0ca8fd;
  padding-right: 32px;
  transition: color 0.3s;
}

.btn-close:hover {
  color: #58c3fe;
}

.btn-close:hover .icon {
  fill: #58c3fe;
}

.btn-close .icon {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  transition: fill 0.3s;
}

.btn-main {
  padding: 21px 51px;
  border: 2px solid white;
  border-radius: 5px;
  font-family: "Roboto Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 30px;
  line-height: 1;
  color: #fff;
}

.btn-main:hover, .btn-main:focus {
  top: 5px;
}

.btn-main:hover:after, .btn-main:focus:after {
  display: none;
}

.btn-main:after {
  content: '';
  display: block;
  position: absolute;
  left: -2px;
  right: -2px;
  top: 100%;
  margin-top: -1px;
  height: 6px;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  background-color: #fff;
}

.btn-category {
  min-width: 282px;
  text-transform: lowercase;
  font-family: "Roboto Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 30px;
  line-height: 1;
  color: rgba(255, 255, 255, 0.4);
