    margin-top: 40px;
  }
}

.btn-category:hover, .btn-category:focus {
  color: white;
}

.btn-category:hover:before, .btn-category:focus:before {
  width: 100%;
}

.btn-category:hover:after, .btn-category:focus:after {
  height: 100%;
}

.btn-category:before, .btn-category:after {
  content: '';
  display: block;
  position: absolute;
  border-style: solid;
  border-color: #fff;
  transition: all 0.3s;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

.btn-category:before {
  width: 0;
  border-width: 4px 0 4px 0;
}

.btn-category:after {
  border-width: 0 4px 0 4px;
  height: 0;
}

.btn-category:nth-of-type(odd) {
  margin-right: 28px;
}

.btn-category span {
  position: relative;
  z-index: 2;
}

.btn-category__inner {
  border: 4px solid rgba(255, 255, 255, 0.4);
  padding: 26px 15px;
}

@media only screen and (max-width: 900px) {
