.commercial {
	position: relative;
	z-index: 5;
	font-size: 0;
	@include lg-min {
		padding-left: (316px / 1850px) * 100%; }
	&__col {
		display: inline-block;
		vertical-align: top;
		@include lg {
			text-align: right;
			width: 50%; }
		@include md {
			text-align: left; }
		@include sm {
			width: 100%;
			text-align: center; }
		+ .commercial__col {
			@include sm-min {
				padding-left: 56px; }
			@include lg {
				text-align: left; }
			@include sm {
				margin-top: 22px;
				text-align: center; } } }
	&__btn {
		margin-top: 56px;
		@include lg-min {
			padding-left: 47px; }
		@include sm {
			text-align: center;
			margin-top: 40px; }
		.btn-primary {
			line-height: 1;
			padding: 27px 72px;
			@include xs {
				font-size: 20px;
				padding: 20px 30px; } } }
	.list {
		max-width: 384px;
		display: inline-block;
		text-align: left;
		@include lg {
			max-width: 500px; }
		@include sm {
			width: 100%; } } }
