.footer {
	position: relative;
	background-color: #03162d;
	text-align: left;
	&__in {
		max-width: 1570px;
		margin: auto;
		position: relative;
		padding: 63px 15px;
		@include clr;
		@include md {
			padding: 20px 15px; }
		@include sm {
			padding: 40px 15px; } }
	&__col1 {
		float: left;
		padding-top: 36px;
		@include md {
			padding-top: 30px; }
		@include sm {
			padding: 0; } }
	&__col2 {
		float: right;
		@include sm {
			float: none;
			clear: both; } }
	&__logo {
		@include bebas-bold;
		font-size: 60px;
		line-height: 1;
		display: inline-block;
		vertical-align: top;
		@include md {
			font-size: 42px; } }
	&__content {
		max-width: 460px;
		@include md {
			max-width: 370px; } }
	&__content-text {
		opacity: 0.36;
		@include bebas-reg;
		margin-top: 12px;
		line-height: 1.2; } }
.payments {
	font-size: 0;
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: 8px;
	&__item {
		display: inline-block;
		vertical-align: top;
		margin-left: 20px;
		@include md {
			margin-left: 10px; }
		+ .payments__item {
			@include md-min {
				margin-left: 22px; } } } }
.info-list {
	font-size: 0;
	line-height: 1;
	@include bebas-reg;
	@include sm-min {
		padding: 26px 0 36px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		position: relative;
		padding: 0 70px;
		@include lg {
			padding: 0 20px; }
		@include md {
			padding: 0 30px 0 0;
			&:last-of-type {
				padding-right: 0; } }
		@include sm {
			padding: 20px 20px 0 0; }
		&:not(:first-child) {
			&::before {
				content: '';
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11);
				width: 2px;
				background-color: #ffffff;
				opacity: 0.05; } } }
	&__item_blue {
		.contact-list span,
		.info-list__title {
			color: $pink; } }
	&__title {
		color: $pink;
		font-size: 24px;
		@include md {
			font-size: 20px; } } }
.contact-list {
	font-size: 24px;
	margin-top: 18px;
	@include md {
		font-size: 20px; }
	&__item {
		&:not(:first-child) {
			margin-top: 12px; } }
	span, p {
		display: inline-block;
		vertical-align: baseline; }
	span {
		width: 72px;
		padding-right: 10px;
		color: $pink;
		@include md {
			max-width: 55px; } } }
