.list {
	&__item {
		&:not(:last-child) {
			margin-bottom: 10px;
			@include xs-min {
				margin-bottom: 22px; } } }
	&__item_top-vertical {
		.list__item-icon,
		.list__item-text {
			vertical-align: top; } }
	&__item-icon,
	&__item-text {
		display: table-cell;
		vertical-align: middle; }
	&__item-icon {
		@include xs {
			display: none; } }
	&__item-text {
		@include roboto-light;
		font-size: 18px;
		line-height: 1.2;
		@include xs-min {
			padding-left: 20px; }
		@include md {
			line-height: 1.6; } } }
