@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.3s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

//max-width
@mixin lg {
  @include r(1600) {
    @content; } }
@mixin md {
  @include r(1200) {
    @content; } }
@mixin sm {
  @include r(900) {
    @content; } }
@mixin xs {
  @include r(600) {
    @content; } }

//min-width
@mixin lg-min {
  @include rmin(1601) {
    @content; } }
@mixin md-min {
  @include rmin(1201) {
    @content; } }
@mixin sm-min {
  @include rmin(901) {
    @content; } }
@mixin xs-min {
  @include rmin(601) {
    @content; } }
