.company {
	line-height: 1;
	max-width: 430px;
	display: inline-block;
	vertical-align: middle;
	@include md {
		max-width: 360px; }
	@include xs {
		max-width: 250px; }
	&__logo {
		@include hide-text; }
	&__text {
		@include css-lock(24, 32, 900, 1920, "font-size");
		margin-top: 5px;
		@include xs {
			font-size: 20px; } }
	&__button {
		margin-top: 46px; }
	img {
		@extend %img;
		@include lg {
			width: 80%;
			margin: auto; } } }
