.webmasters {
	position: relative;
	z-index: 5;
	font-size: 0;
	@include rmin(1601) {
		padding-left: 38%; }
	&__col {
		display: inline-block;
		vertical-align: top;
		width: 50%;
		@include rmin(1601) {
			max-width: 534px;
			&:nth-of-type(1) {
				max-width: 380px; } }
		@include lg {
			&:nth-of-type(1) {
				text-align: right; } }
		@include md {
			&:nth-of-type(1) {
				text-align: left; } }
		@include sm {
			width: 100%;
			text-align: center;
			&:nth-of-type(1) {
				text-align: center; } }

		+ .webmasters__col {
			@include sm-min {
				padding-left: 66px; }
			@include sm {
				margin-top: 22px; } }
		.list {
			text-align: left;
			display: inline-block;
			@include lg {
				max-width: 450px; }
			@include md {
				max-width: 500px;
				width: 100%; } } }
	&__btn {
		margin-top: 56px;
		padding-left: 47px;
		.btn-primary {
			padding: 27px 72px;
			line-height: 1;
			@include xs {
				font-size: 20px;
				padding: 20px 30px; } }
		@include lg {
			padding-left: 0; }
		@include sm {
			text-align: center;
			margin-top: 40px; } } }
