.out {
	min-height: 320px;
	overflow: hidden; }
.inner {
	@extend %inner;
	position: relative;
	@include clr;
	@include sm {
		padding: 0 10px; } }
.perfomance {
	background-color: #7200d4;
	.inner {
		height: 100vh;
		min-height: 450px;
		@include vertical; } }
.work {
	.inner {
		min-height: 100vh;
		padding-bottom: 106px;
		@include md {
			padding-top: 80px;
			padding-bottom: 80px; } } }
.section3 {
	background-color: #03162d;
	position: relative;
	@include md-min {
		&:before {
			content: '';
			display: block;
			position: absolute;
			background: url(../img/lines-bg.png) no-repeat center;
			background-size: cover;
			top: 0;
			right: 0;
			left: 38px;
			height: 1218px;
			z-index: 2; } }
	.inner {
		padding-top: 84px;
		padding-bottom: 112px;
		height: 100vh;
		min-height: 850px;
		z-index: 2;
		@include md {
			min-height: auto;
			height: auto;
			padding-top: 50px;
			padding-bottom: 50px; } }
	.title-primary {
		margin-bottom: 30px;
		@include sm-min {
			margin-bottom: 81px; } } }
